.hero-main {
	padding-top: 78px;
	padding-bottom: 88px;

	&__title {
		@include text-50-light;
		margin-bottom: 30px;

		@include desktop {
			@include h1-200-light;
			margin-bottom: 76px;
		}
	}

	&__content-box {
		display: grid;
		grid-template: auto / 1fr 1.18fr;
		column-gap: 50px;
		max-width: 1075px;
	}

	&__content-col {
	}

	&__description {
		@include h3-40-light;
		margin-bottom: 20px;
	}

	&__address {
		@include text-16-light;
	}

	&__text {
		@include text-16-light;
		margin-bottom: 10px;
	}
}
