.partners {
	padding-top: 112px;
	padding-bottom: 100px;

	&__title {
		@include h3-40-light;
		margin-bottom: 50px;
	}

	&__grid {
		display: grid;
		grid-template:  250px / repeat(3,1fr);
		grid-auto-rows: 250px;
		grid-gap: 50px 60px;
	}

	&__elem {
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: $color-gray-light;
	}

	&__img {
		max-width: 100%;
	}
}
