.map-section {
	padding-top: 96px;
	padding-bottom: 100px;

	&__map-box {
		margin-bottom: 50px;
	}

	&__img {
		display: block;
		width: 100%;
	}

	&__row {
		display: grid;
		grid-template: auto / repeat(2, minmax(0,43%));
	}

	&__col {
	}

	&__name {
		@include text-31-light;
		margin-bottom: 5px;
	}

	&__text {
		@include text-20-light;
		max-width: 580px;
	}

	&__small-text {
		@include text-16-light;
	}
}
