.apartment-plan {
	padding-top: 89px;
	padding-bottom: 100px;

	&__top-box {
		display: grid;
		grid-template: auto / minmax(0,1fr) minmax(0, 1.45fr);
		grid-column-gap: 52px;
		margin-bottom: 50px;
	}

	&__title {
		@include text-31-light;
		margin-bottom: 30px;

		@include desktop {
			@include h3-40-light;
			margin-bottom: 0;}
	}

	&__text {
		@include text-16-light;
		max-width: 556px;
	}

	&__slider-box {
		padding: 50px;
		background-color: #F8F8F8;
	}

	&__slider {
		margin-bottom: 18px;
	}

	&__nav-row {
		display: flex;
		justify-content: space-between;
	}
}
