.gallery1 {
	&__slider {
	}

	&__slider-elem {
	}

	&__slide {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		user-select: none;
	}

	&__slide-box {
		display: inline-block;
	}

	&__slide-top {
		display: flex;
		justify-content: space-between;
		margin-bottom: 10px;
	}

	&__slide-text {
		display: inline;
		font-size: 14px;
		line-height: 1.5;
		font-weight: 300;
		letter-spacing: .02em;
	}

	&__slide-bottom {
		display: flex;
		justify-content: flex-end;
		margin-top: 10px;
	}

	&__slide-img {
		width: 100%;
	}
}

.dd {
	opacity: .5;
}
