.estate-description {
	padding-top: 200px;
	padding-bottom: 60px;

	&__title {
		@include h2-70-light;
		margin-bottom: 46px;
	}

	&__grid {
		display: grid;
		grid-template: auto / 1.07fr 1fr 1fr;
	}

	&__grid-elem {
		padding-top: 51px;
		padding-bottom: 37px;
		border-top: 1px solid $color-gray-medium;

		&--m2 {
			grid-column: 3/4;
			grid-row: 1/3;
		}

		&--pl {
			padding-left: 32px;
		}
	}

	&__value {
		@include h2-70-light;
	}

	&__small-text {
		@include text-16-light;
		display: inline-block;
		max-width: 220px;

		.estate-description__value & {
			margin-left: 5px;
		}
	}

	&__text {
		@include text-31-light;
		margin-bottom: 8px;
	}

	&__img {
		max-width: 100%;
		margin-top: -30px;
		margin-bottom: 20px;
	}
}
